:root {
  --main-bg-color: #ecf1f1;
  --timer-border-color: #00979d;
  --timer-outline-color: #a5f2ee;
  --timer-text-color: #005c5f;
  --timer-elapsed-color: #7fcbcd;
}

.sinthwave {
  --main-bg-color: #3c345c;
  --main-text-color: #b8aec8;
  --timer-border-color: #5da4a6;
  --timer-outline-color: #5da4a6;
  --timer-text-color: #d30cb8;
  --timer-elapsed-color: #6df1d8;
}

body {
  background-color: var(--main-bg-color);
  color: var(--main-text-color);
  padding: 0 20px;
  font-family: Open Sans, Lucida Grande, Lucida, Verdana, sans-serif;
  font-style: normal;
  font-weight: 600;
}

.timer {
  border: 1px solid var(--timer-border-color);
  height: 40px;
  cursor: pointer;
  color: var(--timer-text-color);
  border-radius: 4px;
  margin-bottom: 15px;
  display: block;
  position: relative;
  overflow: hidden;
}

.timer--active {
  box-shadow: var(--timer-outline-color) 0px 0px 0 3px;
}

.timer__text {
  text-transform: capitalize;
  justify-content: space-between;
  padding: 10px;
  font-weight: 600;
  line-height: 20px;
  display: flex;
  position: relative;
}

.timer__time {
  text-transform: capitalize;
}

.timer__elapsed {
  background-color: var(--timer-elapsed-color);
  width: 0%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

@font-face {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 400;
  src: url("https://content.arduino.cc/fonts/OpenSans-Regular-webfont.woff") format("woff");
}

@font-face {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  src: url("https://content.arduino.cc/fonts/OpenSans-Semibold-webfont.woff") format("woff");
}

